.tile-close {
  background-color: #304859 !important;
  color: #304859 !important;
  border: 1px solid white;
  flex: 1 0 23%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tile-close img {
  visibility: hidden;
}

.tile-open {
  background-color: #bcced9 !important;
  color: #000000 !important;
  flex: 1 0 23%;
}

.tile-open img {
  display: block;
  height: 100%;
  width: 100%;
  padding: 15px;
}
